import React from "react";
import { Link } from "react-router-dom";
import Navigation from "./Navigation";
import MobileNavigation from "./MobileNavigation";
import blogCSS from "./allblogStyle.module.css"

function PrototypeArt() {
  return (
    <div className={blogCSS.mainContainer}>
      <h1 className={blogCSS.regularHomeButton}>
        <Link to = "/">North South</Link>
      </h1>

      <Navigation />
      <MobileNavigation />

      <div className={blogCSS.color}></div>

      <div className={blogCSS.articleBackground}>
        <div className={blogCSS.container}>
          <h1 className={blogCSS.pageHeadings}>Art</h1>
          <br></br>
          <p>The link to the prototype internet art is provided below. Further development of the idea is under the Blog section. The aim is to turn the initial demonstration into the experience of the whole website.</p>
          <h1><a href = "https://nkadimengk.github.io/WSOA4175A_884200/internet-art.html" target="blank" >Internet Art Prototype</a></h1>
        </div>
      </div>
    </div>
  );
}

export default PrototypeArt;
