import React from 'react'
import { Link } from 'react-router-dom'
import blogArtCSS from './blogArt.module.css'

import Blog from '../../pages/Blog'
import Home from '../../pages/Home'
import DigitalDivide from './DigitalDivide'
import MobileNavigation from './MobileNavigation'
 

function BlogArt() {
    return (
        <div className={blogArtCSS.mainContainer}>
        <h1 className={blogArtCSS.regularHomeButton}>
          
          <Link to  = "/">North South</Link>
        </h1>
  
        <nav className={blogArtCSS.restOfNavButtons}></nav>
  
        <MobileNavigation/>
        <div className={blogArtCSS.color}></div>
        <div className={blogArtCSS.North}>
          <div className={blogArtCSS.NorthContainer}>
            <div className={blogArtCSS.NorthImages}>
              <iframe
                className={blogArtCSS.NorthBlogs}
                src={"https://nkadimengk.github.io/WSOA4175A_884200/blog-art.html"}
                frameborder="0"
                title="Blogs in the North side"
                allowfullscreen 
              ></iframe>
            </div>
  
            <div className={blogArtCSS.ActivitiesButtons}>
              <div className={blogArtCSS.RechargeButton}>
                <button className={blogArtCSS.DBtn}>Data</button>
              </div>
              <div className={blogArtCSS.ReadButton}>
                <button className={blogArtCSS.ReadBtn}>Read</button>
              </div>
              <div className={blogArtCSS.WorkButton}>
                <button className={blogArtCSS.WBtn}>Work</button>
              </div>
            </div>
            <div className={blogArtCSS.Balances}>
              <p className={blogArtCSS.DataBalance}>Balance: 0GB</p>
              <p>Increase visibility</p>
              <p className={blogArtCSS.Wallet}>Wallet: R0</p>
            </div>
          </div>
        </div>
  
        <div className={blogArtCSS.South}>
          <div className={blogArtCSS.SouthContainer}>
            <div className={blogArtCSS.SouthImages}>
              <iframe
                className={blogArtCSS.SouthBlogs}
                src={"https://nkadimengk.github.io/WSOA4175A_884200/blog-art.html"} 
                frameborder="0"
                title="Blogs in the South side"
                allowfullscreen
              ></iframe>
            </div>
  
            <div className={blogArtCSS.SouthActivitiesButtons}>
              <div className={blogArtCSS.SouthRechargeButton}>
                <button className={blogArtCSS.SouthDBtn}>Data</button>
              </div>
              <div className={blogArtCSS.SouthReadButton}>
                <button className={blogArtCSS.SouthReadBtn}>Read</button>
              </div>
              <div className={blogArtCSS.SouthWorkButton}>
                <button className={blogArtCSS.SouthWBtn}>Work</button>
              </div>
            </div>
            <div className={blogArtCSS.SouthBalances}>
              <p className={blogArtCSS.SouthDataBalance}>Balance: 0GB </p>
              <p>Increase visibility</p>
              <p className={blogArtCSS.SouthWallet}>Wallet: R0 </p>
            </div>
          </div>
        </div>
      </div>
    )
}

export default BlogArt
